<template>
  <div> Main侧边栏导航菜单<br/> {{this.$store.getters.sideWith}}
  </div>
</template>
<script>
export default {
  methods: {
    toggle: function () {
      if (this.$store.getters.sideWith === 180) {
        this.$store.commit('setSideWith', 40)
      } else {
        this.$store.commit('setSideWith', 180)
      }
    }
  }
}
</script>



